<template>
    <div id="show-blogs">
      <h1>博客总览</h1>
      <input type="text" v-model="search" placeholder="输入搜索内容">
      <div v-for="blog in filteredBlogs" v-theme:column="'wide'" class="single-blog">
        <router-link v-bind:to="'blog/' + blog.id">
          <h2 v-rainbow>{{blog.title | to-uppercase}}</h2>
        </router-link>
        <article>
          {{blog.body | snippet}}
        </article>
      </div>
    </div>
</template>

<script>
    export default {
        name: "show-blogs",
        data() {
          return {
            blogs: [],
            search: ''
          }
        },
        created() {
          let url = 'http://jsonplaceholder.typicode.com/posts';
          // let url = './../static/posts.json';
          this.$http.get(url)
            .then(function (data) {
              let blog_data = data.body.slice(0,10);
              this.blogs = blog_data;
            });
        },
        computed: {
          filteredBlogs: function () {
            return this.blogs.filter((blog) => {
              return blog.title.match(this.search);
            })
          }
        },
        filters: {
          'to-uppercase': function (value) {
            return value.toUpperCase();
          }
        },
        directives: {
          'rainbow': {
            bind(el, binding, vnode) {
              el.style.color = '#' + Math.random().toString(16).slice(2, 8);
            }
          }
        }

    }
</script>

<style scoped>
#show-blogs{
  margin: 0 auto;
  max-width: 800px;
}

.single-blog{
  background: #ccc;
  padding: 20px;
  box-sizing: border-box;
  margin: 20px 0;
  border: 1px dotted #aaa;
}

#show-blogs a {
  color: #444;
  text-decoration: none;
}

input[type='text'] {
  padding: 8px;
  width: 100%;
  box-sizing: border-box;
}

</style>



